import './index.css'
import { message } from 'antd'
import registerServiceWorker from './registerServiceWorker'
import { Rluy } from './rluyconfig'

Rluy.router(require('./router'))

Rluy.onError(e => {
    console.log('', e)
    message.error('')
})

Rluy.run(document.getElementById('root'))

registerServiceWorker()



